body {
    font-size     : 16px;
    font-family   : Roboto;
    background    : linear-gradient(90deg, rgba(238, 174, 202, 1) 0%, rgba(148, 187, 233, 1) 100%);
}

body.dark-theme {
    background-color: #535353;
    background: linear-gradient(90deg, rgba(33,44,97,0.9332983193277311) 0%, rgba(57,78,97,1) 40%, rgba(75,46,122,1) 74%);
}

body.dark-theme input {
    background-color: #393e46;
}

body.dark-theme dialog.popup {
    background: linear-gradient(90deg, rgba(33,44,97,0.9332983193277311) 0%, rgba(57,78,97,1) 40%, rgba(75,46,122,1) 74%);
}

body.dark-theme dialog.popup button.close-icon svg line {
    stroke: black;
}

body.dark-theme input::placeholder {
    color: black;
}

body.light-theme {
    background-color: white;
}

body.light-theme input{
    background: #E5E5E5;
}

body.light-theme dialog.popup{
    background:  linear-gradient(90deg, rgba(238, 174, 202, 1) 0%, rgba(148, 187, 233, 1) 100%);;
}

.chat-container {
    max-width: 320px;
    margin   : auto;
}

.chat-wrapper {
    font-family   : Roboto;
    font-size     : 16px;
    font-weight   : 400;
    line-height   : 19px;
    letter-spacing: 0em;
    text-align    : left;

    position: relative;
}

.theme-switch {
    position: absolute;
    top     : 15px;
    left    : 340px;
}

.theme-switch.dark-theme path {
    fill: #fddc9b;
}

.button {
    border        : solid 1px #000000;
    font-family   : Roboto;
    font-size     : 16px;
    font-weight   : 400;
    line-height   : 19px;
    letter-spacing: 0em;
    text-align    : left;
    padding       : 6px 8px;
}

.dialog-window {
    border        : solid 1px #000000;
    display       : flex;
    flex-direction: column-reverse;
    min-height    : 437px;
    max-height    : 500px;
    padding       : 8px;
    margin-bottom : 8px;
    overflow-y    : scroll;
}

.dialog-window::-webkit-scrollbar {
    width: 10px;
}

.dialog-window::-webkit-scrollbar-track {
    background: #9370DB;
}

.dialog-window::-webkit-scrollbar-thumb {
    background-color: #F7D4EB;
    border-radius   : 20px;
    border          : 3px solid #9370DB;
}

input {
    border : solid 1px #000000;
    padding: 6px 8px;
}

.unread {
    background: #F6F6F6;
}

.depatch-time {
    font-family    : Roboto;
    font-size      : 14px;
    font-style     : italic;
    font-weight    : 300;
    line-height    : 16px;
    letter-spacing : 0em;
    color          : #606470;
    display        : block;
    display        : flex;
    justify-content: space-between;
    position       : relative;
}

.depatch-time .my-message {
    margin-left: auto;
}

.button-container {
    display        : flex;
    justify-content: space-between;
    padding-top    : 10px;
    padding-bottom : 10px;
}

.sending-container {
    display        : flex;
    justify-content: space-between;
    padding-top    : 10px;
    padding-bottom : 10px;
}

.chat-list {
    display       : flex;
    flex-direction: column;
}

.chat-list.light-theme>li.delivered {
    background-color: #E5E5E5;
}

.chat-list.dark-theme>li.delivered {
    background-color: #393e46;
}

.message-row {
    width        : fit-content;
    margin-bottom: 8px;
    padding      : 6px;
}

.sender {
    margin-right: 5px;
}

.my {
    align-self: flex-end;
}

.input-message {
    display        : flex;
    justify-content: space-between;
    gap            : 26px;
}

.add-message {
    width: 260px;
}

.popup {
    margin: auto;
}

.settings-container {
    width     : 273px;
    min-height: 319px;
    padding   : 14px;
    position  : relative;
    top       : 0;
    right     : 0;
    bottom    : 0;
    left      : 0;
    margin    : auto;
}

.settings-title {
    display        : flex;
    justify-content: space-between;
    align-items    : baseline;
    margin-bottom  : 15px;
    padding-top    : 5px;
}

.popup-input-message {
    padding-top    : 15px;
    display        : flex;
    justify-content: space-between;
    gap            : 26px;
}

::backdrop {
    background: #535353;
}

.authorization-container {
    width     : 273px;
    min-height: 319px;
    padding   : 14px;
    position  : relative;
    top       : 0;
    right     : 0;
    bottom    : 0;
    left      : 0;
    margin    : auto;
}

.autorization-title-container {
    display        : flex;
    justify-content: space-between;
    align-items    : baseline;
    padding-top    : 5px;
    gap            : 5px
}


.email-container {
    padding-top: 50px;
}

.email-button-container {
    display        : flex;
    justify-content: space-between;
    align-items    : baseline;
    padding-top    : 10px;
    gap            : 5px
}

.enter-code {
    width: 110px;
}

.poput-add-email {
    width: 245px;
}

.confirmation-container {
    display        : flex;
    justify-content: space-between;
    align-items    : baseline;
    padding-top    : 5px;
    gap            : 5px
}

.confirmation-popup-container {
    width     : 273px;
    min-height: 319px;
    padding   : 14px;
    position  : relative;
    top       : 0;
    right     : 0;
    bottom    : 0;
    left      : 0;
    margin    : auto;

}

.code-container {
    padding-top: 50px;
}

.popup-add-code {
    width: 245px;
}

.code-button {
    padding-top: 10px;
}

.loader-container {
    position: relative;
}

.loader {
    width        : 48px;
    height       : 48px;
    border       : 3px dotted #9370DB;
    border-style : solid solid dotted dotted;
    border-radius: 50%;
    display      : inline-block;
    position     : absolute;
    left         : 45%;
    bottom       : 200px;
    box-sizing   : border-box;
    animation    : rotation 2s linear infinite;
}

.loader::after {
    content         : '';
    box-sizing      : border-box;
    position        : absolute;
    left            : 0;
    right           : 0;
    top             : 0;
    bottom          : 0;
    margin          : auto;
    border          : 3px dotted #F7D4EB;
    border-style    : solid solid dotted;
    width           : 24px;
    height          : 24px;
    border-radius   : 50%;
    animation       : rotationBack 1s linear infinite;
    transform-origin: center center;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

@keyframes rotationBack {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(-360deg);
    }
}

.hideElement {
    display: none;
}

.loading {
    opacity       : 0.7;
    pointer-events: none;
}

.blocked-users {
    margin-top    : 20px;
    display       : flex;
    flex-direction: column;
    gap           : 5px;
}

.blocked-users li {
    display              : grid;
    grid-template-columns: 1fr 20px;
    align-items          : center;
}